  <div class="modal-document">
<h4 class="fw-bold">Krew na śniegu – rozpoczyna się bitwa o Monte Cassino</h4>

    <p class="fw-bold">Bitwę o masyw Monte Cassino uważa się powszechnie za najkrwawsze starcie na froncie zachodnim II wojny światowej.
    Bilans walk nie byłby tak straszny, gdyby na ich początku alianccy dowódcy pamiętali o naukach Hannibala i Napoleona
    i nie popełniali tak karygodnych błędów, jak chociażby zbombardowanie klasztoru na Monte Cassino 15 lutego 1944
    roku.
    </p>
    <p>Po sukcesach wojsk alianckich w Afryce Północnej postanowiono iść za ciosem i rozpocząć walkę po drugiej stronie
    Morza Śródziemnomorskiego. W nocy z 9 na 10 lipca 1943 roku na plażach Sycylii wylądowali żołnierze amerykańscy,
    brytyjscy, kanadyjscy i australijscy. Niemcy i Włosi byli kompletnie zaskoczeni, ale ich obrona szybko się
    pozbierała i sprzymierzeni dopiero na początku września mogli przeskoczyć z Sycylii na południowy kraniec „włoskiego
    buta”. Desant pod Salerno 9 września 1943 roku osłaniały między innymi polskie niszczyciele i okręty podwodne.
    Szczególnie wsławił się wśród nich ORP „Dzik”, który w ciągu dwóch tygodni patrolowania Morza Liguryjskiego
    storpedował 14 nieprzyjacielskich okrętów.
    </p>
    <p>Lądowanie sprzymierzonych w południowych Włoszech było nie tylko sukcesem militarnym, lecz także przełomem
    politycznym – Włosi obalili faszystowski rząd Benito Mussoliniego i przeszli na stronę aliantów. Adolf Hitler nie
    zamierzał jednak darować Włochom tej zdrady, ani pozostawić w opałach swego „faszystowskiego guru” Mussoliniego.
    Kiedy 8 września Włosi ogłosili oficjalnie kapitulację, 40 niemieckich dywizji zajęło cały kraj, a większość
    sojuszniczej do tej pory armii włoskiej rozbrojono i zamknięto za drutami obozów jenieckich.
    </p>
<h4 class="fw-bold">Droga ślimaka po brzytwie</h4>
    <p>Człowiekiem, który miał udowodnić aliantom, że lądując we Włoszech wstąpili jednocześnie do piekła, został
    feldmarszałek Luftwaffe Albert Kesserling. Dowodził on najbitniejszymi żołnierzami w armii niemieckiej – strzelcami
    spadochronowymi, zwanymi przez nieprzyjaciół „zielonymi diabłami”. W tym czasie spadochroniarze niemieccy nie mieli
    już wielu okazji do desantów spadochronowych, lecz jako doborowa piechota okazali się świetni w obronie, o czym
    mieli się przekonać alianci, m.in. pod Monte Cassino. Kesserling, by powstrzymać marsz sprzymierzonych do Rzymu,
    stworzył na tym kierunku kilka pozycji obronnych: zimową, Bernhardta i Gustawa. Ta ostatnia opierała się na masywie
    Monte Cassino, który był postrachem dla wszystkich armii atakujących Italię od czasów starożytnych. To m.in. słynny
    wódz Kartagińczyków, Hannibal, wolał iść okrężną drogą przez Alpy i wytracić tam prawie wszystkie swoje słonie, niż
    narażać się na pewną klęskę u „wrót ryglujących Rzym”. Natomiast zdobywca Włoch, Napoleon Bonaparte, dał swoim
    ewentualnym naśladowcom prostą radę: „Włochy to but. Trzeba wchodzić w nie od góry”. W sztabie armii sprzymierzonych
    o tej przestrodze pamiętał jedynie rodak cesarza Francuzów, dowódca Francuskiego Korpusu Ekspedycyjnego, generał
    Alfons Juin. Znając przebieg włoskiej kampanii Napoleona, Juin był prawie pewien, że alianckie natarcie w kierunku
    Rzymu zostanie zatrzymane w rejonie masywu Monte Cassino. Dlatego proponował głównodowodzącemu sprzymierzonych, gen.
    Dwightowi D. Eisenhowerowi, by zamiast wikłać się w walki na południu Włoch, przeprowadzić desanty na północy – pod
    Livorno i Ankoną, czyli wejść we „włoski but” od góry. Niestety, tej mądrej rady nie chciał słuchać ani Eisenhower,
    ani inni anglosascy dowódcy. To dowodziło wyraźnie, że alianccy sztabowcy nie zdawali sobie sprawy z przeszkód
    terenowych, jakie tam będą na nich czekać. Ponadto Italia kojarzyła im się z turystycznymi folderami „słonecznego
    raju” i nie brali pod uwagę, że zima i wiosna zmieni włoskie drogi w bagno, a wezbrane rzeki w miejsca nie do
    przebycia. Ponadto, żołnierze feldmarszałka Kesserlinga świetnie wykonali saperską pracę, przygotowując umocnienia
    polowe wymienionych linii obronnych. Warto wspomnieć, że Niemcy oparli się przy tym na umocnieniach polowych Wojska
    Polskiego, które przyszło im z wielkim trudem przełamywać we wrześniu 1939 roku.
    </p>
    <p>To wszystko spowodowało, że ofensywa aliancka wyraźnie wyhamowała w październiku 1943 roku. Kolejne miesiące na
    Półwyspie Apenińskim były już dla sprzymierzonych powolnym „posuwaniem się ślimaka po ostrzu brzytwy”. Na początku
    listopada 1943 roku po ciężkich walkach alianci przesunęli się na kolejną niemiecką linię obrony na rzece Rapido,
    czyli pozycję Bernhardta. Następnie dotarli do miasteczka Cassino i do podnóża masywnego wzgórza Monte Cassino, na
    którym zobaczyli mury słynnego benedyktyńskiego opactwa. Niestety, nie mieli zbyt wiele czasu na podziwianie jednego
    z architektonicznych arcydzieł, gdyż ze wzgórza lunął na nich ogień z niemieckich dział i karabinów maszynowych.
    Okazało się, że strzelcy spadochronowi ryglują nim jedyną drogę pozwalającą na swobodne przemieszczanie się wojsk –
    szosę biegnącą wzdłuż wybrzeża w kierunku Rzymu. Tutaj definitywnie skończyły się sukcesy aliantów z drugiej połowy
      roku 1943.
    </p>
<h4 class="fw-bold">Żarłoczne góry</h4>
    <p>W opisywanym okresie, czyli na przełomie 1943 i 1944 roku, działania na kolejnych liniach niemieckiej obrony we
    Włoszech nabierały charakteru walk pozycyjnych, tak dobrze znanych z czasów I wojny światowej czy niektórych
    kampanii na froncie wschodnim w II wojnie światowej. Kiedy główne siły walczących stron tkwiły dłuższy czas
    naprzeciw siebie, strzelając od czasu do czasu z artylerii i cekaemów, rozpoczęła się „robota patrolowa”. Tak też
    było nad rzekami Garigliano i Rapido, a jednymi z najlepszych zwiadowców alianckich okazali się polscy komandosi z 1
    Samodzielnej Kompanii Commando, dowodzonej przez mjr. Władysława Smrokowskiego. Kompania od 12 grudnia 1943 roku
    walczyła we włoskich górach w składzie brytyjskiej 2 Brygady do Zadań Specjalnych gen. Toma Churchilla. Żołnierze
    majora Smrokowskiego odznaczyli się m.in. rajdem na wzgórze Monte Valle Martina, które zdobyli razem z Brytyjczykami
    z Queens Regiment 17 stycznia 1944 roku, ale ten sukces nie czynił jednak wiosny w obozie alianckim w nowym 1944
    roku. Pod koniec stycznia polscy komandosi ze sporymi stratami w zabitych i rannych zostali wycofani z frontu i
    niedługo potem włączeni do 2 Korpusu Polskiego.
    </p>
    <p>Drobne sukcesy alianckich komandosów czy zwiadowców nie mogły przysłonić prawdy, że sytuacja wojsk sprzymierzonych u
    podnóża Monte Cassino jest zła. Pierwsza próba zdobycia wzgórza została podjęta już 5 listopada 1943 roku przez
    brytyjską 56 Dywizję Piechoty i amerykańską 3 Dywizję Piechoty. Atak zakończył się niepowodzeniem. Równocześnie
    brytyjska 8 Armia gen. Bernarda L. Montgomery’ego podjęła ofensywę po wschodniej stronie Półwyspu Apenińskiego, ale
    również tutaj wojska niewiele posunęły się naprzód poza niemiecką pozycję zimową, ku linii Bernharda nad rzeką
    Sangro. Tymczasem pod koniec listopada kolejną próbę przełamania obrony masywu Monte Cassino podjęła amerykańska 5
    Armia gen. Marka W. Clarka. Po morderczych walkach w grudniu udało się zdobyć wzgórze Monte Camino, a pod koniec
    stycznia 1944 roku Monte Samourco i Monte Luka. Wszystko to Amerykanie okupili ogromnymi stratami. Grafika z
    niemieckiej ulotki propagandowej, na której włoskie góry z paszczami pełnymi zębów jak u rekinów pożerają całe
    oddziały anglosaskie, stała się rzeczywistością.
    </p>
    <p>Wobec hekatomby własnych oddziałów alianccy dowódcy postanowili podjąć próbę niespodziewanego wejścia na tyły
    niemieckich linii obronnych. W operacji „Single” („Żwir”) 22 stycznia 1944 roku wysadzono silny desant w rejonie
    Anzio-Nettuno, około 50 kilometrów na południowy wschód od Rzymu. Niemcy zostali kompletnie zaskoczeni, ale zamiast
    ruszyć szybko do przodu, dowodzący siłami desantowymi amerykański generał John P. Lucas zaczął umacniać przyczółek
    na plażach. To dało czas feldmarszałkowi Kesserlingowi na wzmocnienie obrony pod Anzio, a Amerykanie utknęli tam na
    dobre – dokładnie na cztery miesiące. Gen. Lucas za swoje „zasługi” został zwolniony z dowództwa i odesłany do
    Stanów Zjednoczonych. Niestety, po zaprzepaszczeniu tej szansy znowu trzeba było planować kolejne natarcie na masyw
    Monte Cassino, który Niemcy po skróceniu swych linii obrony nazwali pozycją Gustawa.
    </p>
<h4 class="fw-bold">Największy błąd włoskiej kampanii</h4>
    <p>Po wykrwawieniu się Amerykanów w kolejnym ataku na Monte Cassino, na początku lutego 1944 roku, to zadanie,
    wydawałoby się niewykonalne, otrzymali Nowozelandczycy pod dowództwem gen. Bernarda Freyberga. Po wielu naradach w
    sztabie Freyberg oświadczył, że nie pośle swych ludzi do walki, póki nie zostanie zbombardowany klasztor
    Benedyktynów na wzgórzu Monte Cassino. Nowozelandczyk, wyczytawszy w jakimś opracowaniu popularnonaukowym, że przez
    wieki była to twierdza nie do zdobycia, uznał, że klasztor jest największym problemem, który stoi na drodze do
    sukcesu jego wojsk. Nie była to jednak tylko i wyłącznie twierdza, lecz przede wszystkim jedna z kolebek
    chrześcijaństwa, w której murach zgromadzono wielkie dzieła sztuki i piśmiennictwa, a obie walczące strony
    deklarowały, że w swych działaniach wojennych będą chronić europejskie dziedzictwo kulturowe na terenie Włoch. Z
    tego też powodu pochodzący z katolickiej, bawarskiej rodziny Kesserling (skądinąd niewahający się w 1939 roku
    barbarzyńsko bombardować Warszawy) wzbraniał się przed fortyfikowaniem klasztoru. Ostatecznie Amerykanie zezwolili
    na bombardowanie, ale wcześniej poinformowali o tym zakonników i Stolicę Apostolską. Dzięki Niemcom ewakuowano z
    klasztoru ludzi i cenne zbiory.
    </p>
    <p>Rankiem 15 lutego 1944 roku 255 amerykańskich „latających fortec” zrzuciło na Monte Cassino ponad 350 ton bomb
    burzących, które zdmuchnęły grube mury klasztoru niczym domek z kart. Jak wielkim błędem był ten nalot, by nie
    powiedzieć – głupotą, generał Freyberg przekonał się bardzo szybko. Jego waleczni Maorysi i Gurkhowie, mimo pogardy
    śmierci, z której słynęli, nie zdołali wyrzucić z bunkrów niemieckich strzelców spadochronowych ani 17 lutego, ani w
    czasie kolejnego krwawego natarcia – 15 marca 1944 roku. Spadochroniarze, zająwszy zbombardowane ruiny klasztoru,
    uczynili z niego prawdziwą twierdzę nie do zdobycia. Dowódca sił alianckich we Włoszech, gen. Harold Alexander,
    stwierdził wtedy: „Niestety, walczymy z najlepszymi żołnierzami na świecie”. Natomiast dowódca 5 Armii, gen. Mark
    Clark, przyznał po wojnie bez ogródek: „Zbombardowanie klasztoru stanowiło nie tylko niepotrzebny błąd
    psychologiczny z propagandowego punktu widzenia, było też z wojskowego punktu widzenia błędem pierwszej klasy.
    Utrudniło tylko zadanie i zwiększyło straty w ludziach i sprzęcie, przynosząc także stratę czasu”. Historycy do dziś
    dyskutują nad tym fenomenem lekkomyślności, a niektórzy posuwają się nawet do tak daleko idących wniosków, jak ten,
    że był to przejaw odwiecznej rywalizacji obozu protestanckiego (Anglików i Amerykanów) z papistami.
    </p>
    <p>Abstrahując od tych kontrowersyjnych teorii, należy stwierdzić, że w maju 1944 roku do zdobycia ruin Monte Cassino
    ruszyli „papiści”, a w siłach sprzymierzonych – Polacy. Kiedy bowiem na przełomie 1943 i 1944 roku we włoskich
    górach prowadzili swe rajdy komandosi z polskiej 1 Samodzielnej Kompanii Commando, w Tarencie na południu Włoch, ze
    statków i okrętów schodziły na ląd pierwsze oddziały 2 Korpusu Polskiego gen. Władysława Andersa. Już niedługo i
    jego żołnierze mieli wziąć udział w bitwie najlepszych żołnierzy na świecie.
    </p>
